
import { defineComponent, PropType } from 'vue';
import { WordCard } from '@/utils/interfaces';

export default defineComponent({
  name: 'WordCard',
  props: {
    info: {
      type: Object as PropType<WordCard>,
      required: true,
    },
  },
  setup(props) {
    const languages = props.info.languages.reduce((acc, cur, i) => {
      if (i === 0) {
        return `${acc} ${cur}`
      }
      return `${acc} Â· ${cur} `;
    }, '');

    return {
      languages,
    }
  },
});
