
import { defineComponent, PropType } from 'vue';
import { Header } from '@/utils/interfaces';

// TODO: get rid of the inline-style
export default defineComponent({
  name: 'Header',
  props: {
    header: {
      type: Object as PropType<Header>,
      required: true,
    },
    color: {
      type: String,
      default: 'purple-darkest',
    },
  },
});
