
import { defineComponent, ref } from 'vue';
import { navLinks, homeSection } from '@/utils/data';
import { Typing } from './components';


export default defineComponent({
  name: 'Home',
  components: { Typing },
  setup() {
    const { home: { anchor } } = navLinks;
    const root = ref(null);

    return {
      anchor,
      ...homeSection,
      root,
    }
  },
});
