
import { defineComponent, PropType, ref } from 'vue';
import { PhotoCard } from '@/utils/interfaces';

export default defineComponent({
  name: 'PhotoCard',
  props: {
    info: {
      type: Object as PropType<PhotoCard>,
      required: true,
    },
  },
  setup() {
    const hover = ref<boolean>(false);

    return {
      hover,
    }
  },
});
