
import { defineComponent, ref } from 'vue';

import { Header } from '@/common-components';
import { aboutSection, navLinks } from '@/utils/data';

export default defineComponent({
  name: 'About',
  components: { Header },
  setup() {
    const root = ref(null);

    return {
      about: navLinks.about.anchor,
      ...aboutSection,
      root,
    }
  },
});
